import { h, Component } from 'preact';
import style from './capabilities';
import SkillIcon from './skill-icon';
import SkillDetail from './skill-detail';

export default class Capabilities extends Component {
	render() {
		return (
			<div class={style.capabilities}>
        <div class={'page-header ' + style.header}>How can we help you?</div>
				<h2>We have the skills you are looking for.</h2>
				<p>We have deep experience in creating highly scalable technology that has been used by millions of people. Chances are youve used a Codingscape software product and you didnt even know it! Our products span the software world from robots to web apps and APIs to publisher software books.</p>

				<div class={style.skillmenu}>
					<SkillIcon icon="code" name="Front End" target="#front-end" />
					<SkillIcon icon="terminal" name="Back End" target="#back-end" />
					<SkillIcon icon="mobile-alt" name="Mobile" target="#mobile" />
					<SkillIcon icon="cloud" name="Cloud" target="#cloud" />
				</div>

				<div class="jumbo">
					Excellence is not a skill, it's an attitude.
					<cite>Ralph Marston</cite>
				</div>

				<SkillDetail icon="code" anchor="front-end">
					<h2>Front End</h2>
					<p>Front End technology has never moved at a faster pace than it does today. There are new frameworks popping up seemingly every day and they are often accompanied by sophisticated back end tooling to transform and optimize front end assets for maximum performance while not sacrificing maintainability. We can help you pick the right technology for your new project, or we can work with what you have and make it better!</p> 
					
					<p>We work with: React, Redux, Vue.js, Angular, jQuery, ES6+ and legacy vanilla JavaScript, build tools and transpilers such as webpack, Babel, Browserify and many others.</p>
				</SkillDetail>

				<SkillDetail icon="terminal" anchor="back-end">
					<h2>Back End</h2>
					<p>Our team feels at home in front of a command prompt and can help you build backend systems that are secure, reliable and will handle as much traffic as your application requires. This goes far beyond just writing solid code, but also to picking the right back-end services and network topology and configuring them properly to make sure they can fail over gracefully and there are no timeouts or dreaded gateway errors. Our developers are OWASP certified and can work with your team on securing your codebase. We can help you build API's, microservices, or entire websites.</p>
					<p>We work with: Node.js, Ruby / Rails, PHP, Perl, Java, Python, C and have worked with others.</p>
					<p>Backend services: PostgreSQL, MongoDB, Redis, MySQL / MariaDB, DynamoDB.</p>
				</SkillDetail>

				<SkillDetail icon="mobile-alt" anchor="mobile">
					<h2>Mobile</h2>
					<p>More people are accessing content and making purchases via mobile devices and tablets than ever before. For an increasing segment of the population, this is the only way they connect to the internet! For this segment, you may as well be invisible if you don't have a mobile presence. We can help you develop a native mobile app for Apple or Android devices or develop a great site with mobile-first optimizations that will load quickly, look great and feel comfortable to use on a touch device. We can even optimize your existing site to be mobile friendly.</p>
					<p>We work with: React Native, Swift, Objective-C, Kotlin and Java.</p>
				</SkillDetail>

				<SkillDetail icon="cloud" anchor="cloud">
					<h2>Cloud Services</h2>
					<p>Some of the biggest advantages of hosting your site in the cloud are automation, virtually unlimited scaling, and high availability. We can work with you to develop a strategy for hosting your application on Amazon Web Services that will give you peace of mind. Scale your server capacity up and down to handle traffic spikes, paying for only what you need. Deploy copies of your whole application and all the services you use for testing with a few button pushes. Run your app in multiple available zones so that even if there is an AWS outage in your primary zone, your app will be resilient.</p>
					<p>We work primarily with AWS including: EC2, Elastic Container Service, Docker, S3, Lambda, CloudFront, CloudFormation, S3, IAM and more.</p>
				</SkillDetail>

			</div>
		);
	}
}



