import { h, Component } from 'preact';
import style from './capabilities';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default class SkillDetail extends Component {
	render() {
    const { icon, anchor } = this.props;
    const currentLocation = location && location.hash;
    let activeClass = '';
    if (currentLocation && currentLocation === '#' + anchor ) {
      activeClass = ' ' + style.active;
    }
		return (
			<div class={style.skilldetail}>
        <a name={anchor}></a>
        <div class={style.skilldetailicon + activeClass}>
          <FontAwesomeIcon icon={icon} />
        </div>
        <div class={style.skilldetailtext}>
          {this.props.children}
        </div>
			</div>
		);
	}
}



